    if (Util.debug) console.log('this.typeStr : ' + typeStr);
    
    this.type = typeStr;

    this.myType = this.getMyType(typeStr);
    if (Util.debug) console.log('this.myType : ' + JSON.stringify(this.myType));
    
  }

  getMyType(myTypeStr:string):TypeInterface {
    var objLength = Object.keys(MbtiModule.types).length;  
    if (Util.debug) console.log('objLength : ' + objLength.toString());

    var myType:TypeInterface = {
      type: '',
      id: 0,
      title: '',
      simpleText: '',
      description: '',
      imageUrl: ''
    };

    for (var i = 0; i < objLength; i++) {
      if (MbtiModule.types[i].type == myTypeStr) {
        myType = MbtiModule.types[i];
      }
    }

    return myType;
  }


