// Mutations
// Extend 'Mutations' class with 'FooState' type
class PersonalitiesMutations extends Mutations<PersonalitiesState> {
  // increment(payload: number) {
  //   // Mutations instance has 'state' property.
  //   // You update 'this.state' by mutating it.
  //   this.state.count += payload
  // }

  incrementValue(payload: string){
    switch(payload) {
      case 'e': this.state.e++; break;
      case 'i': this.state.i++; break;
      case 's': this.state.s++; break;
      case 'n': this.state.n++; break;
      case 't': this.state.t++; break;
      case 'f': this.state.f++; break;
      case 'j': this.state.j++; break;
      case 'p': this.state.p++; break;
    }
  }
}

