  redirectTypePage(btnNo: number){
      console.log('More than numOfQa!');
      this.typeCalcMode = true;

      const sexType:string = (btnNo == 0) ? 'm' : 'f';

      // スコア計算
      const typeString: string = this.getType();
      const typeId: number = this.getTypeId(typeString); 

      // リダイレクト
      // this.$router.push({ path: '/type/' + typeId.toString() + '?s=' + sexType });
      this.$router.push({ path: '/type/' + typeId.toString() + '/' + sexType });

  }

  
  getValue(qaIndex: number, buttonNo: number): string {
    return this.qaArray[qaIndex].answer[buttonNo].value;
  }

  getType(): string {
    var typeString = '';

		typeString += (this.$store.state.e >= this.$store.state.i) ? "E" : "I";
		typeString += (this.$store.state.s >= this.$store.state.n) ? "S" : "N";
		typeString += (this.$store.state.t >= this.$store.state.f) ? "T" : "F";
    typeString += (this.$store.state.j >= this.$store.state.p) ? "J" : "P";
    
    return typeString;
  }

  getTypeId(typeString: string): number {
    var typeId:number = 0;

    for (var i:number=0; i < MbtiModule.typesArray.length; i++) {
      if (MbtiModule.typesArray[i] == typeString) {
        typeId = i;
      }
    }

    return typeId;
  }

